.statList {
     
  display: flex;
  flex-direction: row;
  padding: 0;
}
.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 60px;
  width: 60px;
}
.label {
  margin-bottom: 7px;
  font-size: 12px;
  color: var(--main-white);
}
.percentage {
  font-size: 16px;
  font-weight: 400;
  color: var(--main-white);
}